adFilesText[data-v-3564206f] {\n    color: #616161;\n    font-style: normal;\n    font-weight: 400;\n    font-size: 18px;\n    letter-spacing: 0px;\n    opacity: 1;\n    margin-left: 20px;\n    margin-top: 10px;\n\n    font-family: \"ROBOTO\";\n}\n.fileTypeQjson[data-v-3564206f] {\n    font-size: 18px;\n    padding-left: 5px;\n}\n.fileTypeImage[data-v-3564206f] {\n    font-size: 18px;\n    padding-left: 5px;\n}\n.fileTypeEntity[data-v-3564206f] {\n    font-size: 18px;\n    padding-left: 5px;\n}\n.fileTypeBpmn[data-v-3564206f] {\n    font-size: 18px;\n    padding-left: 5px;\n}\n.fileTypeProcess[data-v-3564206f] {\n    font-size: 18px;\n    padding-left: 5px;\n}\n.unacceptableFileType[data-v-3564206f] {\n    color: red;\n    font-size: 18px;\n    padding-left: 5px;\n}\n.uploadArea[data-v-3564206f] {\n    text-align: center;\n    padding-top: 25px;\n    border: 1px dashed #a1a1a1;\n    height: 100px;\n}\n.cancelBtn[data-v-3564206f] {\n    background: none;\n    border: none;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 16px;\n    letter-spacing: 0px;\n    opacity: 1;\n    margin-left: 50px;\n    font-family: \"ROBOTO\";\n    margin-right: 10px;\n    color: #757575;\n    letter-spacing: 0.02px;\n}\n.loader[data-v-3564206f] {\n    border: 3px solid #f3f3f3;\n    border-radius: 70%;\n    border-top: 3px solid #3498db;\n    width: 12px;\n    height: 12px;\n    -webkit-animation: spin-3564206f 1s linear infinite;\n    animation: spin-3564206f 1s linear infinite;\n}\n.ml-6[data-v-3564206f] {\n    margin-left: 6px;\n}\n\n/* Safari */\n@-webkit-keyframes spin-3564206f {\n0% {\n        -webkit-transform: rotate(0deg);\n}\n100% {\n        -webkit-transform: rotate(360deg);\n}\n}\n@keyframes spin-3564206f {\n0% {\n        transform: rotate(0deg);\n}\n100% {\n        transform: rotate(360deg);\n}\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/importModelDialog.vue"],"names":[],"mappings":";AAofA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,kBAAkB;AACtB;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,iBAAiB;IACjB,cAAc;AAClB;;AAEA;;;;;;;;;;;GAWG;AAEH;IACI,kBAAkB;IAClB,YAAY;IACZ,UAAU;IACV,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,mBAAmB;IACnB,qBAAqB;AACzB;AAEA;IACI,eAAe;IACf,0BAA0B;AAE9B;AAEA;IACI,eAAe;IACf,mBAAmB;IACnB,kBAAkB;IAClB,eAAe;IACf,mBAAmB;IACnB,UAAU;AACd;AAEA;IACI,eAAe;IACf,mBAAmB;IACnB,kBAAkB;IAClB,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,eAAe;IACf,cAAc;IACd,mBAAmB;IACnB,kBAAkB;IAClB,eAAe;IACf,mBAAmB;IACnB,UAAU;AACd;AAEA;IACI,mBAAmB;IACnB,YAAY;IACZ,kBAAkB;IAClB,YAAY;IACZ,YAAY;AAChB;AAEA;IACI,eAAe;AACnB;AAEA;IACI,gBAAgB;IAChB,yBAAyB;IACzB,aAAa;IACb,kBAAkB;IAClB,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,mBAAmB;IACnB,UAAU;IACV,qBAAqB;IACrB,iBAAiB;IACjB,YAAY;IACZ,iBAAiB;AACrB;AAEA;IACI,cAAc;IACd,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,mBAAmB;IACnB,UAAU;IACV,iBAAiB;IACjB,gBAAgB;;IAEhB,qBAAqB;AACzB;AAEA;IACI,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,UAAU;IACV,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,0BAA0B;IAC1B,aAAa;AACjB;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,mBAAmB;IACnB,UAAU;IACV,iBAAiB;IACjB,qBAAqB;IACrB,kBAAkB;IAClB,cAAc;IACd,sBAAsB;AAC1B;AAEA;IACI,yBAAyB;IACzB,kBAAkB;IAClB,6BAA6B;IAC7B,WAAW;IACX,YAAY;IACZ,mDAA0C;IAC1C,2CAAkC;AACtC;AAEA;IACI,gBAAgB;AACpB;;AAEA,WAAW;AACX;AACI;QACI,+BAA+B;AACnC;AAEA;QACI,iCAAiC;AACrC;AACJ;AAEA;AACI;QACI,uBAAuB;AAC3B;AAEA;QACI,yBAAyB;AAC7B;AACJ","sourcesContent":["<script setup lang=\"ts\">\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { ICreateModel, ICreateModelParam } from \"../../../../domain/useCase/ICreateModel\";\nimport { IApplication, IFolder, IModel, IModule } from \"../../../../domain/model/models\";\nimport { INewItemProps } from \"../../../../domain/presentation/dialogs/INewItemProps\";\nimport { ref } from \"@vue/runtime-dom\";\nimport { computed } from \"@vue/reactivity\";\nimport { TooltipImpl } from \"../../impls/tooltipImpl\";\nimport { IFormValidator, IFormValidatorOptions, IFormValidatorResponseItem } from \"../../../../domain/infrastructure/IFormValidator\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport { IQuickEditor } from \"../../../../domain/objects/editors/IQuickEditor\";\nimport { BinaryHelper } from \"@stechquick/algae/lib/helpers/binaryHelper\";\nimport { ILogger } from \"../../../../domain/infrastructure/ILogger\";\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst tooltip = container.resolve<TooltipImpl>(TooltipImpl);\nconst formValidator = container.resolve<IFormValidator>(IFormValidator);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst logger = container.resolve<ILogger>(ILogger);\nconst nameValidations: IFormValidatorOptions = { minAndMaxLength: { min: 2, max: 40 }, nonSpaceCharacter: true, customRegex:{ regex: /^([a-zA-Z0-9-_]+)$/, message: \"Available characters: A-Z, a-z, 1-9, '-' and '_'\"} };\n\nconst applications = ref(viewModel.user!.selectedOrganization!.applications);\n\ninterface IImportFile {\n    name: string;\n    content: string;\n    isDuplicate: boolean;\n    isAcceptable: boolean;\n    nameValidationExceptions: Array<IFormValidatorResponseItem>;\n    type: \"entity\" | \"qjson\" | \"process\" | \"bpmn\" | \"flow\" | \"png\" | \"jpg\" | \"jpeg\" | \"svg\" | \"gif\" | \"json\" | \"woff\" | \"woff2\" | \"ttf\" |\"css\";\n}\n\ninterface INewItemPropsInline extends INewItemProps {\n    parent: IModule | IFolder | IApplication;\n}\n\nconst props = defineProps<INewItemPropsInline>();\n\nconst fileItems = ref<Array<IImportFile>>([]);\n\nconst loadingIcon = ref(false);\nconst emit = defineEmits([\"close\"]);\nconst entityNumber = ref(0);\nlet acceptableFileTypes: Array<IImportFile[\"type\"]> =\n    props.parent.objectType == \"application\"\n        ? [\"qjson\" ]\n        : props.parent.objectType == \"module\" || props.parent.objectType == \"folder\"\n        ? Object.entries(applications.value)\n              .filter((application) => application[1])\n              .map((application) => (application[0] == \"quick\" ? \"qjson\" : (application[0] as IImportFile[\"type\"])))\n        : [];\nacceptableFileTypes = acceptableFileTypes.concat([\"gif\", \"png\", \"jpeg\", \"jpg\", \"svg\", \"json\", \"woff\", \"woff2\", \"ttf\"])\n\nconst binaryFileTypes: Array<string> = [\"gif\", \"png\", \"jpeg\", \"jpg\", \"svg\"]; // dictionary yap.\n\nif(viewModel.user!.selectedOrganization!.features?.multipleCss) {\n    acceptableFileTypes = acceptableFileTypes.concat([\"css\"]);\n}\n\nconst hasEntity = ref(props.parent?.children?.some((i) => i.objectType == \"model\" && i.modelType == \"entityDesigner\") == true);\n\nfunction checkDuplicate(name: string, type: IImportFile[\"type\"]) {\n    let extensionType = (type == \"json\") ? \"lottie\" : type;\n\n    const purify = (str:string) => str.includes('.') ? str.split(\".\").slice(0, -1).join(\".\") : str;\n    const isDuplicate = (items: Array<IModel | IModule | IFolder>) => items.filter(e => e.objectType == 'model')\n        .map(e => e as IModel)\n        .some((i) => i.name == purify(name) && i.modelType == extensionType)\n\n    return isDuplicate(props.parent.objectType == 'module' ? props.parent?.children : viewModel.studio.items)\n}\n\nfunction getInputAccept() {\n    let fileTypes = acceptableFileTypes;\n    if (props.parent.objectType == \"application\"){\n        fileTypes = fileTypes.filter(fileType => (applications as any)[fileType] != true);\n    }\n    const types = acceptableFileTypes.map(fileType => `.${fileType}`).join(\", \");\n    logger.log({ level: \"debug\", message: \"accept types: \" + types });\n    return types;\n}\n\nfunction checkExtension(file: string) {\n    const extension = file.substring(file.lastIndexOf(\".\") + 1, file.length);\n    return extension as IImportFile[\"type\"];\n}\n\nasync function selectedFiles() {\n    var item = document.getElementById(\"fileInput\") as HTMLInputElement | null;\n    for (var i = 0; i < item?.files?.length!; i++) {\n        const file = item?.files?.item(i);\n        const extensionOfFile = checkExtension(file!.name);\n        const fileNameWithoutExtension = file!.name.substring(0, file!.name.lastIndexOf(\".\"));\n        \n        const isExist = fileItems.value.some((i) => i.name == file?.name) || fileItems.value.some((i) => i.name.substring(0, file!.name.lastIndexOf(\".\")) == fileNameWithoutExtension) ;\n       \n        if (isExist) { continue; }\n        \n            const isDuplicate = checkDuplicate(file!.name, extensionOfFile) || checkDuplicate(fileNameWithoutExtension, extensionOfFile);\n\n            const nameValidationExceptions: Array<IFormValidatorResponseItem> = formValidator\n                .validate(fileNameWithoutExtension, nameValidations)\n                .filter((item) => !item.isValid);\n            const isAcceptable = acceptableFileTypes.includes(extensionOfFile);\n\n        const isBinaryFile = BinaryHelper.checkBinaryFile(extensionOfFile);\n\n        if(isBinaryFile){\n            const binaryFileContent = await getBinaryFileContent(file!);\n            fileItems.value.push({\n                name: file!.name,\n                content: binaryFileContent,\n                nameValidationExceptions,\n                isDuplicate,\n                isAcceptable,\n                type: extensionOfFile,\n            });\n        }\n        else{\n            const text = await file!.text();\n            fileItems.value.push({\n                name: file!.name,\n                content: text,\n                nameValidationExceptions,\n                isDuplicate,\n                isAcceptable,\n                type: extensionOfFile,\n            });\n        }\n\n            if (extensionOfFile == \"entity\") {\n                entityNumber.value = entityNumber.value + 1;\n            }\n        }\n    tooltip.startTooltip();\n}\n\nasync function getBinaryFileContent(file: File): Promise<string> {\n    const fileReader = new FileReader();\n\n    return new Promise((resolve, reject) => {\n        fileReader.onload = () => {\n            resolve(fileReader.result as string);\n        };\n        fileReader.readAsDataURL(file);\n    });\n}\n\nasync function importSelectedModels() {\n    emit(\"close\");\n    if (isButtonDisabled.value) return;\n    loadingIcon.value = true;\n\n    let images: { path: string; name: string; }[] = [];\n    fileItems.value.forEach(async (item) => {\n        if (item.isDuplicate || !item.isAcceptable || item.nameValidationExceptions.length != 0) {\n            return;\n        }\n\n        const createObject: ICreateModelParam = {\n            name: item.name.split(\".\").slice(0, -1).join(\".\"),\n            parentObjectId: props.parent.ID,\n            modelBody: [{ key: item.type, model: item!.content }],\n            modelType: \"qjson\",\n            modelAdditionals: { qjsonType: \"qjson\" },\n        };\n\n        switch (item.type) {\n            case \"qjson\":\n                createObject.modelType = \"qjson\";\n                createObject.modelAdditionals = { qjsonType: \"qjson\" };\n                break;\n            case \"entity\":\n                if (isEntityRuleBlock(item)) {\n                    return;\n                }\n                createObject.modelType = \"entityDesigner\";\n                createObject.modelAdditionals = {};\n                break;\n            case \"bpmn\":\n                createObject.modelType = \"bpmn\";\n                createObject.modelAdditionals = { isAutoGenerated: false, isModifiedByUser: true };\n                break;\n\n            case \"process\":\n                createObject.modelType = \"process\";\n                createObject.modelAdditionals = {};\n                break;\n            case \"png\":\n                createObject.name += \".png\"\n                createObject.modelType = \"png\";\n                createObject.modelAdditionals = {};\n                break;\n            case \"jpg\":\n                createObject.name += \".jpg\" \n                createObject.modelType = \"jpg\";\n                createObject.modelAdditionals = {};\n                break;\n            case \"jpeg\":\n                createObject.name += \".jpeg\"\n                createObject.modelType = \"jpeg\";\n                createObject.modelAdditionals = {};\n                break;\n            case \"svg\":\n                createObject.name += \".svg\"\n                createObject.modelType = \"svg\";\n                createObject.modelAdditionals = {};\n                break;\n            case \"gif\":\n                createObject.name += \".gif\"\n                createObject.modelType = \"gif\";\n                createObject.modelAdditionals = {};\n                break;\n            case \"json\":\n                createObject.modelType = \"lottie\";\n                createObject.extension = \"json\";\n                break;\n            case \"css\":\n                createObject.name += \".css\"\n                createObject.modelType = \"css\";\n                createObject.key = \"style\";\n                createObject.modelAdditionals = {};\n                break;\n            case \"woff\":\n                createObject.name += \".woff\";\n                createObject.modelType = \"woff\";\n                createObject.modelAdditionals = {};\n                break;\n            case \"woff2\":\n                createObject.name += \".woff2\";\n                createObject.modelType = \"woff2\";\n                createObject.modelAdditionals = {};\n                break;\n            case \"ttf\":\n                createObject.name += \".ttf\";\n                createObject.modelType = \"ttf\";\n                createObject.modelAdditionals = {};\n                break;\n        }\n\n        try {\n            let model = await executor.executeUseCase<ICreateModel>(ICreateModel, createObject);\n            images.push({path: formatPath(model.ID), name: model.name});\n\n            if(model?.modelType === \"css\" && item!.content) {\n                const targetEditor = container.resolve<IQuickEditor>(IQuickEditor);\n                targetEditor.replaceStyle!(\"addStyle\", model.ID, item!.content);\n            }\n        }\n        catch{\n            console.log(\"error\")\n        }\n    \n        loadingIcon.value = false;\n    });\n       \n\n    // if(images.length != 0) {\n    //     const targetEditor = container.resolve<IQuickEditor>(IQuickEditor);\n    //     await targetEditor.setImages(images);\n    // }\n      \n}\nfunction formatPath(id:string){\n    return `<<img:${id}>>`;\n}\nfunction computedIcon(fileType: string, isAcceptable?: boolean) {\n    switch (fileType) {\n        case \"qjson\":\n            if (!isAcceptable)\n            return `mdi mdi-palette fileTypeQjson`;\n        case \"entity\":\n            if (!isAcceptable)\n            return `mdi mdi-database fileTypeEntity`;\n        case \"bpmn\":\n            if (!isAcceptable)\n            return `mdi mdi-vector-polyline fileTypeBpmn`;\n        case \"process\":\n            if (!isAcceptable)\n            return `mdi mdi-auto-fix fileTypeProcess`;\n        case \"png\":\n        case \"jpg\":\n        case \"jpeg\":\n        case \"svg\":\n        case \"gif\":\n        case \"json\":\n        case \"woff\":\n            if (!isAcceptable)                       \n            return `mdi mdi-format-font fileTypeImage`;\n        case \"woff2\":\n            if (!isAcceptable)\n            return `mdi mdi-format-font fileTypeImage`;\n        case \"ttf\":\n            if (!isAcceptable)\n            return `mdi mdi-format-font fileTypeImage`;\n        case \"css\":\n            return \"mdi mdi-music-accidental-sharp ml-6\";\n        // case \"woff\":\n        //     return \"mdi mdi-format-font fileTypeImage\";\n        default:\n    }\n}\nfunction computeIconColor(fileType: string) {\n    let color = \"#212121\"\n    switch (fileType) {\n        case \"qjson\":\n            color = \"#449dd1\"\n            return color;\n        case \"entity\":\n                color = \"rgb(233, 114, 76)\"\n            return  color;\n        case \"bpmn\":\n                color = \"rgb(0, 124, 119)\"\n            return  color;\n        case \"process\":\n                color = \"rgb(0, 124, 119)\"\n            return  color;\n        case \"png\":\n        case \"jpg\":\n        case \"jpeg\":\n        case \"svg\":\n        case \"gif\":\n        case \"json\":\n        case \"css\":\n            return  color;\n        default:\n    }\n\n}\nfunction removeItem(file: IImportFile, selectedFiles: Array<any>) {\n    if (file.type == \"entity\") {\n        entityNumber.value = entityNumber.value - 1;\n    }\n    const index = selectedFiles.indexOf(file);\n    selectedFiles.splice(index, 1);\n    tooltip.startTooltip();\n}\n\nfunction isEntityRuleBlock(item: IImportFile) {\n    if (item.type != \"entity\") {\n        return false;\n    }\n\n    const isEntityRuleBlock = hasEntity.value || entityNumber.value > 1;\n    return isEntityRuleBlock;\n}\n\nfunction anyNotDuplicateItem() {\n    const retVal = fileItems.value.some((i) => i.type != \"entity\" && !i.isDuplicate && i.isAcceptable);\n    return retVal;\n}\n\nfunction anyAcceptableItem() {\n    const retVal = fileItems.value.some((i) => i.isAcceptable && i.nameValidationExceptions.length == 0);\n    return retVal;\n}\n\nconst isButtonDisabled = computed(() => {\n    if (fileItems.value.length == 0) {\n        return true;\n    }\n\n    if (!anyAcceptableItem()) {\n        return true;\n    }\n\n    if (loadingIcon.value) {\n        return true;\n    }\n\n    if (fileItems.value.length > entityNumber.value && anyNotDuplicateItem()) {\n        return false;\n    }\n\n    if (fileItems.value.some((i) => i.type != \"entity\")) {\n        return true;\n    }\n\n    const isEntityRuleBlock = entityNumber.value > 1 || (hasEntity.value && entityNumber.value > 0);\n    return isEntityRuleBlock;\n});\n</script>\n\n<template>\n    <div class=\"fileInputDiv\">\n        <div class=\"fileUploadDiv col-12\" onclick=\"document.getElementById('fileInput').click()\">\n            <div class=\"col-12 uploadArea\" style=\"display: flex; padding-left: 150px; padding-top: 35px\">\n                <div\n                    class=\"mdi mdi-cloud-upload-outline\"\n                    style=\"text-align: center; font-size: 25px; color: #a1a1a1; padding-right: 10px; margin-top: -7px\"\n                ></div>\n                <span style=\"font-weight: bold\">Click</span><span style=\"padding-left: 10px\">here to upload your files.</span>\n            </div>\n        </div>\n        <input\n            style=\"display: none\"\n            type=\"file\"\n            id=\"fileInput\"\n            multiple\n            @change=\"selectedFiles()\"\n            :accept=\"getInputAccept()\"\n        />\n    </div>\n    <div class=\"uploadFilesText\">\n                Selected Files <span> ({{ fileItems.length }})</span>\n            </div>\n    <div id=\"selectedFiles\" class=\"selectedItems\">\n        <div v-if=\"fileItems.length > 0\">\n\n            <div v-for=\"fileValues in fileItems\">\n                <div\n                    v-if=\"\n                        !fileValues.isDuplicate &&\n                        !isEntityRuleBlock(fileValues) &&\n                        fileValues.isAcceptable &&\n                        fileValues.nameValidationExceptions.length == 0\n                    \"\n                    class=\"filesValue\"\n                    style=\"color: #000000\"\n                >\n                    <div><i :class=\"computedIcon(fileValues.type)\" :style=\"{color: computeIconColor(fileValues.type)}\"></i></div>\n                    <div class=\"col-10\" style=\"margin-left: 10px; text-overflow: ellipsis; overflow: hidden\">\n                        {{ fileValues.name.split(\".\").slice(0, -1).join(\".\") }}\n                    </div>\n\n                    <div class=\"col-1\" style=\"text-align: end; padding-left: 30px\">\n                        <i\n                            v-if=\"fileValues && !loadingIcon\"\n                            class=\"mdi mdi-close removeIcon color: #212121 \"\n                            :style=\"computedIcon(fileValues.type) ? '' : 'margin-left: 23px;'\"\n                            @click=\"removeItem(fileValues, fileItems)\"\n                        ></i>\n\n                        <div v-if=\"loadingIcon\" style=\"padding-top: 7px\" class=\"loader\"></div>\n                    </div>\n                </div>\n\n                <div\n                    v-if=\"\n                        fileValues.isDuplicate ||\n                        isEntityRuleBlock(fileValues) ||\n                        !fileValues.isAcceptable ||\n                        fileValues.nameValidationExceptions.length != 0\n                    \"\n                    class=\"filesValue\"\n                    style=\"color: red ; border: 1px solid red\"\n                >\n                    <div><i :class=\"computedIcon(fileValues.type, (fileValues.isDuplicate || isEntityRuleBlock(fileValues) || !fileValues.isAcceptable ||fileValues.nameValidationExceptions.length != 0))\"></i></div>\n                    <div\n                        class=\"col-10\"\n                        data-toggle=\"tooltip\"\n                        :title=\"\n                            isEntityRuleBlock(fileValues)\n                                ? 'Only one entity file is allowed'\n                                : !fileValues.isAcceptable\n                                ? `File type is not supported.`\n                                : fileValues.nameValidationExceptions.length != 0  \n                                ? fileValues.nameValidationExceptions.at(0)!.message \n                                : 'This file is already imported'\n                        \"\n                        placement=\"bottom\"\n                        style=\"margin-left: 10px; text-overflow: ellipsis; overflow: hidden\"\n                    >\n                        {{ fileValues.name.split(\".\").slice(0, -1).join(\".\") }}\n                    </div>\n\n                    <div class=\"col-1\" style=\"text-align: end; padding-left: 30px\">\n                        <i\n                            v-if=\"fileValues && !loadingIcon\"\n                            :class=\"'mdi mdi-close removeIcon' + (computedIcon(fileValues.type) ? '' : ' removeIconReposition')\"\n                            @click=\"removeItem(fileValues, fileItems)\"\n                        ></i>\n\n                        <div\n                            v-if=\"!fileValues.isDuplicate && !isEntityRuleBlock(fileValues) && loadingIcon\"\n                            style=\"padding-top: 7px\"\n                            class=\"loader\"\n                        ></div>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n    <div class=\"importBtnDiv\">\n        <button class=\"cancelBtn\" @click=\"emit('close')\">Cancel</button>\n        <button\n            class=\"importBtn\"\n            :disabled=\"isButtonDisabled\"\n            :style=\"{ backgroundColor: isButtonDisabled ? '#37373733' : '#151233' }\"\n            @click=\"importSelectedModels()\"\n        >\n            UPLOAD\n        </button>\n    </div>\n</template>\n\n<style scoped>\n@import url(\"../../../../styles/IDE3.css\");\n\n.fileInputDiv {\n    margin-left: 20px;\n    margin-top: 20px;\n    margin-right: 20px;\n}\n\n.selectedItems {\n    margin-left: 10px;\n    margin-top: 10px;\n    max-height: 199px;\n    overflow: auto;\n}\n\n/* .selectedItems::-webkit-scrollbar-thumb {\n    background-